* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  padding: 0px;
}

.country {
  font-size: 24px;
}

.svg {
  width: 40px;
  height: auto;

  margin-right: 10px;
}

.main-svg {
  width: 60px;
  height: auto;

  margin-right: 30px;
}

h1 {
  font-size: 44px;
}

.main-list {
  font-size: 24px;
  list-style: none;

  margin: 20px 0px;
  padding: 0;
}
